header {
  min-height: 119px;
  background-color: #f2542d;
  position: relative;
  margin-bottom: 79px;
}
.header {
  display: flex;

  align-items: center;
  padding: 19px 15px 0 15px;
  justify-content: space-between;
}

.header_right {
  display: flex;

  align-items: center;
}
.header_right a {
  height: fit-content;
}
.header_right a {
  margin-right: 13px;
}

.navigate_span {
  margin-left: 25px;
  margin-bottom: 20px;
  font-size: 25px;
}
