.wrapper_table {
  /* width: calc(100% - 200px); */
  /* min-width: 1450px; */
  overflow: scroll;
  font-family: sans-serif;
  font-size: 14px;
}

.wrapper_table table {
  width: 99%;

  margin: 0 auto;

  border-collapse: collapse;
  font-family: Tahoma, Geneva, sans-serif;
  overflow: scroll;
}
.wrapper_table table td {
  padding: 5px;
  overflow-wrap: anywhere;
}
.wrapper_table table thead th {
  background-color: #54585d;
  color: #ffffff;
  font-weight: bold;

  border: 1px solid #fff;
}
.wrapper_table table thead div:first-child {
  margin: 5px;
}
.wrapper_table table tbody td {
  text-align: center;
  color: #636363;
  border: 1px solid #dddfe1;
}
.wrapper_table table tbody tr {
  background-color: #e7e7e7;
}

.wrapper_table table tbody tr:nth-child(odd) {
  background-color: #ffffff;
}
.wrapper_table table tbody tr:hover {
  background-color: #504285;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}
.amount_page {
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrapper_table input {
  width: 70px;
  margin: 5px 0;
}
.wrapper_table input[type="date"] {
  width: 110px;
}
.wrapper_filter input:nth-child(2) {
  margin-left: 10px;
}
.table-icon {
  cursor: pointer;
  margin: auto auto;
  width: 13px;
  height: 13px;
}
.table-icon.false {
  cursor: default;
}
.table_button_wrapper {
  width: 99%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  background-color: #54585d;
  border-collapse: collapse;
}
.table_button_wrapper button,
.table_button_wrapper select {
  padding: 10px;
  color: black;
  background-color: #fff;
  border: 1px solid #dddfe1;
  cursor: pointer;
}
/* form  */

.form_put {
  display: flex;
  flex-wrap: wrap;
}
.put_input_wrapper {
  justify-content: space-around;
  align-items: center;
  height: 45px;
  width: 50%;
  font-size: 16px;
}

.put_input_wrapper span {
  width: 15%;
}
.put_input_wrapper input {
  margin-top: 5px;
  width: 35%;
  height: 25px;
}
.modal_del_content {
  width: fit-content;
  height: fit-content;
  margin: auto auto;
}
.mobile_date {
  display: none;
}
@media only screen and (max-width: 600px) {
  .wrapper_table {
    min-width: 0;
    font-size: 10px;
  }
  .table_button_wrapper {
    width: 100%;
  }
  .wrapper_table table {
    width: 100%;
    display: block;
    overflow-x: scroll;
  }

  .mobile_date {
    display: block;
  }
  .wrapper_filter input:nth-child(2) {
    margin-left: 0;
  }
  .form_put {
    display: block;
  }
  .form_put span {
    font-size: 13px;
  }
  .put_input_wrapper {
    width: 100%;
  }
}

/* photo */

.input-img {
  display: flex;
  cursor: pointer;
  border: 1px solid #d0cfcf;
  border-radius: 4px;
  overflow: hidden;
  width: 380px;
  min-height: 54px;
}

.input-img__right {
  box-sizing: border-box;
  padding-top: 14px;
  padding-bottom: 14px;
  padding-left: 16px;
  text-align: left;
  color: #7e7e7e;
}

.input-img__left {
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.87);
  border-radius: 4px 0px 0px 4px;
  text-align: center;
  padding: 15px 14px;
  background: white;
}

.form-input__none {
  display: none;
}

.error {
  color: #e53e3e;
  margin-top: 8px;
}

.success {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 1024px;
  height: 819px;
  margin: 0 auto;
  position: relative;
}
