.navbar {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 200px;
  background-color: #f1f1f1;
  box-sizing: border-box;
}

.navbar a {
  margin: 20px 10px;
}

.navbar a + div {
  padding: 20px;
  margin-top: 10px;
  background-color: gray;
  display: none;
}
.navbar div a {
  color: #fff;
}
.navbar a.active {
  color: red;
}
.navbar a.active + div {
  display: block;
}
