/* Icon   =====================================  */
.basketIcon {
  display: block;
  position: fixed;
  top: 90%;
  right: 5%;
}
.basketIcon div {
  position: relative;
}
.basketIcon_count {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 26px;
  height: 26px;
  top: -2px;
  right: 9px;
  color: #fff;
  border-radius: 50%;
  background-color: #0e9594;
}
.basketIcon img {
  width: 73px;
  height: 72px;
}

/* Basket header ====================== */

.basket_header {
  display: flex;
  align-items: center;
  padding: 32px 28px;
}
.basket_header img {
  width: 26px;
  height: 26px;
}
.basket_header span {
  text-align: center;
  flex: 1;

  font-size: 25px;
  font-weight: bold;
}

