/* Basket card ====================== */
.basket_card_container {
  height: calc(100vh - 93px - 208px);
  overflow: auto;
}
.basket_card {
  width: 337px;
  height: fit-content;

  box-sizing: border-box;
  padding: 12px;
  margin: 0 auto 12px;
  background-color: #fff;
  border-radius: 10px;
}
.basket_card_product {
  display: flex;
}
.basket_card_product img {
  border-radius: 17px;
  width: 114px;
  height: 114px;
}
.basket_card_text {
  margin-left: 11px;
}
.basket_card_name {
  font-size: 20px;
  font-weight: bold;
}
.basket_card_description {
  font-size: 15px;
  color: #8f8f8f;
}

.basket_card_amount {
  padding: 2px 10px;
  width: fit-content;
  border-radius: 10px;
  color: #fff;
  background-color: #0e9594;
  margin-top: 7px;
}
.additions_title {
  margin-top: 7px;
  font-size: 15px;
  font-weight: bold;
}
.basket_card_bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 5px;
}
.basket_card_bottom button {
  width: fit-content;
  color: #fff;
  padding: 5px 13px;
  background-color: #696969;
  border: none;
  border-radius: 7px;
}
.basket_card_bottom span {
  font-size: 26px;
  color: #f2542d;
}

/* additions card ====================== */

.basket_card_additions_cards {
  display: flex;
  align-items: flex-end;
  overflow-x: scroll;
  gap: 32px;
  /* min-height: 76px; */
  max-width: 100%;
  scrollbar-width: none;
  -ms-overflow-style: none;
}
.additions_cards::-webkit-scrollbar {
  display: none;
}

.basket_card_additions_card {
  background-color: #fff;
  text-align: center;
  padding: 3px 3px 5px 3px;
  border-radius: 10px;
  width: 69px;
}

.basket_card_additions_card img {
  margin: 0 auto;
  width: 47px;
  height: 47px;
}
.basket_card_additions_card span {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 10px;
}
.basket_card_additions_card_title {
  margin-bottom: 10px;
}
/* bottom basket  ====================== */

.basket_bottom {
  position: fixed;
  width: 100%;
  height: fit-content;
  background-color: #fff;
  bottom: 0;
  padding: 15px;
}
.basket_bottom button {
  width: 90%;
  margin: 10px auto 0;
  padding: 12px 0;
  background-color: #f2542d;
  color: #fff;
  border: none;
  border-radius: 10px;
}

/* additions bottom ====================== */
.basket_adittions_title {
  margin-bottom: 7px;
  font-size: 15px;
  font-weight: bold;
}
.additions_cards {
  display: flex;
  align-items: flex-end;
  overflow-x: scroll;
  gap: 32px;
  max-width: 100%;
  height: 100px;
  padding-left: 7px;
  scrollbar-width: none;
  -ms-overflow-style: none;
}
.additions_cards::-webkit-scrollbar {
  display: none;
}

.additions_card {
  position: relative;
  background-color: #fff;
  text-align: center;
  padding: 3px 3px 5px 3px;
  border-radius: 10px;
  width: 69px;
  height: 85px;
}
.additions_check_add,
.additions_check_delete {
  position: absolute;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  top: -7px;
  left: -6px;
}
.additions_check_add {
  background-color: rgba(14, 149, 148, 1);
  color: #fff;
}
.additions_check_delete {
  background-color: #fff;
  color: rgba(14, 149, 148, 1);
}
.additions_card img {
  margin: 0 auto;
  width: 47px;
  height: 47px;
}
.additions_card span:not(:first-of-type) {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 12px;
}
.additions_cards_title {
  margin-bottom: 10px;
}
.additions_card span:last-of-type {
  color: #f2542d;
}
.additions_card.active {
  background-color: #0e9594;
  color: #fff;
}
.additions_card.active span:last-of-type {
  color: #fff;
}
